import React, { useEffect } from "react";
import { Box, Typography, Button } from "@mui/material";
import { useHistory } from "react-router";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { setBalanceGlobal } from "../../redux/reducers/Main";
import {
  getCurrencyW,
  getСurrencySales,
} from "../../redux/reducers/Сurrencies";
import tether from "./../../assets/images/crypto/tetherIcon.webp";
import { useLocation } from "react-router-dom/cjs/react-router-dom.min";

const Balance = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const location = useLocation();

  const { t } = useTranslation();

  const { balance } = useSelector(({ MainReducer }) => MainReducer);
  const { allBalances } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );
  const { sale, withdrawl } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  useEffect(() => {
    dispatch(getСurrencySales());
    dispatch(getCurrencyW(1840));
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    const usdt = allBalances?.find((item) => item.name === "USDT");
    dispatch(setBalanceGlobal(usdt?.balance));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allBalances]);

  return (
    <Box
      display="flex"
      justifyContent="space-between"
      alignItems="center"
      flexDirection={{ xs: "column", sm: "row" }}
      borderBottom="2px solid #E7EAEE"
      pb={{ xs: 2, sm: 4, md: 5 }}
      mb={{ xs: 2, sm: 3, md: 5 }}
    >
      <div>
        <Typography variant="h4" color="#323A46" fontWeight="500">
          {t("YourBalance")}
        </Typography>
        <div>
          {" "}
          <Typography
            variant="h2"
            fontWeight="500"
            sx={{
              display: "flex",
              alignItems: "center",
              lineHeight: 1,
            }}
          >
            <img width="51" src={tether} alt="tether" />
            &#160;
            <Box display="flex" flexDirection="column">
              {balance}
              {location.pathname === "/dashboard/main" ? (
                <span style={{ fontSize: 16 }}>
                  Льготный курс:
                  <b style={{ fontSize: 16 }}> {sale} сом за USDT</b>
                </span>
              ) : (
                location.pathname.includes(
                  "/dashboard/payment-for-services/"
                ) && (
                  <span style={{ fontSize: 16 }}>
                    Курс вывода:
                    <b style={{ fontSize: 16 }}> {withdrawl} сом за USDT</b>
                  </span>
                )
              )}
            </Box>
          </Typography>
        </div>
      </div>
      <Box width={{ xs: "100%", sm: "fit-content" }} mt={{ xs: 2, md: 0 }}>
        <Button
          variant="contained"
          onClick={() => history.push("/dashboard/payment-for-services/106")}
          fullWidth
          size="large"
          color="primary"
        >
          {t("WithdrawFunds")}
        </Button>
        <Box mt={1}>
          <Button
            variant="contained"
            fullWidth
            onClick={() => history.push("/dashboard/replenish")}
            size="large"
            color="secondary"
          >
            {t("TopUpBalance")}
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default Balance;
